.App {
  text-align: center;
}

.App-header {
  background-color: #434343;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2vw;
  color: white;
}

div.Listing {
  width: 80vw;
  background-color: white;
  border-radius: 2.5vw;
  margin-top: 1vw;
}

div.Listing:hover {
  box-shadow: 0 0 10px 0 white;
}

img.Listing {
  height: 20vw;
  border-radius: 2.5vw;
}

img.AutoRu {
  cursor: pointer;
  width: 10vw;
}

a, p, h1, h2, h3, h4, h5 {
  color: #434343;
}

a {
  text-decoration: none;
}

select {
  background-color: white;
  border-radius: 100px;
  outline: none;
  font-size: 1.5vw;
  font-family: sans-serif;
}